import {NgModule, ModuleWithProviders} from '@angular/core';
const NgModule: NeverTypeCheckMe = NgModule;  /* local alias for Closure JSDoc */
const ModuleWithProviders: NeverTypeCheckMe = ModuleWithProviders;  /* local alias for Closure JSDoc */
import {NgbDropdown, NgbDropdownToggle} from './dropdown';
const NgbDropdown: NeverTypeCheckMe = NgbDropdown;  /* local alias for Closure JSDoc */
const NgbDropdownToggle: NeverTypeCheckMe = NgbDropdownToggle;  /* local alias for Closure JSDoc */
import {NgbDropdownConfig} from './dropdown-config';
const NgbDropdownConfig: NeverTypeCheckMe = NgbDropdownConfig;  /* local alias for Closure JSDoc */

export {NgbDropdown, NgbDropdownToggle} from './dropdown';
export {NgbDropdownConfig} from './dropdown-config';

const /** @type {!Array<?>} */ NGB_DROPDOWN_DIRECTIVES = [NgbDropdownToggle, NgbDropdown];
@NgModule({declarations: NGB_DROPDOWN_DIRECTIVES, exports: NGB_DROPDOWN_DIRECTIVES})
export class NgbDropdownModule {
/**
 * @return {!ModuleWithProviders}
 */
static forRoot(): ModuleWithProviders { return {ngModule: NgbDropdownModule, providers: [NgbDropdownConfig]}; }
}
